import {gain_remove_bind} from "../services";
import {GetRequest} from "../utils";

export default {
    namespace: 'app',
    state: {
        _userId: true,
        _loginSpring: 1,
        _authority: -1,//权限
        _shelter: true,//遮挡白版

    },
    reducers: {
        //改变登录状态
        "changeLogin"(state: any, {payload: {user_id}}: any) {
            return {
                ...state,
                _userId: user_id,
                _loginSpring: 0
            }
        },

        //改变白版的显示状态
        "changeShelter"(state: any, {payload: {shelter}}: any) {
            return {
                ...state,
                _shelter: shelter
            }
        }
    },
    effects: {
        //获取登录状态
        * queryLogin(_: null, {call, put}: any) {
            try {
                let result: any = yield new GetRequest().the();
                localStorage.setItem("session", JSON.stringify(result));
                let {user_id} = result;//当user_id为空的时候，为未登录，需要跳转到登录页面
                yield put({
                    type: 'changeLogin',
                    payload: {
                        user_id
                    }
                });
            } catch (err) {
                console.log(`err is ${err}`)
            }

        },
        //解除绑定
        * releaseBind(_: null, {call, put}: any) {
            try {
                let result: any = yield call(gain_remove_bind);
                yield put({
                    type: 'changeLogin',
                    payload: {
                        loginStatus: false
                    }
                });
                return result;
            } catch (err) {
                console.log(`err is ${err}`);
                return Promise.reject(err);
            }

        }
    }
}



